import Vue from 'vue'
import Router from 'vue-router'
import VueCookies from 'vue-cookies'
Vue.use(VueCookies)
Vue.use(Router)
export default new Router({

  base: '/web/',  
  routes: [
   {
        path: '/login',
        name: 'login',
        component: resolve=>(require(["@/components/login"],resolve)),
    },
    {
        path: '/',
        component: resolve=>(require(["@/components/home"],resolve)),
          children: [
              {
                  path: '',
                  name: 'set',
                  component: resolve=>(require(["@/components/Setting"],resolve)),
              },
             {
                  path: '/setting',
                  name: 'setting',
                  component: resolve=>(require(["@/components/Setting"],resolve)),
              },
              {
                  path: '/tables',
                  name: 'tables',
                  component: resolve=>(require(["@/components/Tables"],resolve)),
              }, {
                path: '/column',
                name: 'column',
                component: resolve=>(require(["@/components/Column"],resolve)),
            }, {
                path: '/database',
                name: 'database',
                component: resolve=>(require(["@/components/DataBase"],resolve)),
            },{
                path: '/runsql',
                name: 'runsql',
                component: resolve=>(require(["@/components/RunSql"],resolve)),
            },{
                path: '/showdata',
                name: 'showdata',
                component: resolve=>(require(["@/components/ShowData"],resolve)),
            },{
                path: '/etcd',
                name: 'etcd',
                component: resolve=>(require(["@/components/ETCD"],resolve)),
            },{
                path: '/mongodb',
                name: 'mongodb',
                component: resolve=>(require(["@/components/Mongodb"],resolve)),
            },
          ]
      }
   
  ]
})



