import axios from 'axios'
import { Message } from 'element-ui'
import VueCookies from 'vue-cookies'
import router from "@/router";
import VueI18n from 'vue-i18n';
import { getReqUrl,needbuy,getLang,getValue,setValue } from "@/utils/pub"


let AUTH_TOKEN=(function(){
  
  return getValue("token");
})();
let USERID=(function(){
  return getValue("userid");
})();
let staffid=(function(){
  return getValue("staffid");
})();
let role=(function(){
  var rolee= getValue("role");
  if(rolee==undefined){
     return "1";
  }
  return rolee;
})();

const HTTP = axios.create({
  baseURL: "http://"+getReqUrl()+'/',
});


let message


HTTP.interceptors.request.use(config => {
  //config.withCredentials = true // 允许携带token ,这个是解决跨域产生的相关问题
  config.headers = {
    'Content-Type':'application/x-www-form-urlencoded'
  }
  let token = getValue("token")
  //this.alert_ok("request token="+token)
  //let csrf = store.getters.csrf
  if (token) {
   config.headers = {
    'Authorization': token,
    'Content-Type': 'application/x-www-form-urlencoded'
   }
   config.params = {'userid':getValue("userid"),'staffid':getValue("staffid"),'role':getValue("role"),'apiversion':2.1}
  }else{
    config.params = {'apiversion':2.1}
  }
 
  return config
}, (error) => {
  return Promise.reject(error)
})



// response拦截器
HTTP.interceptors.response.use(
  response => {
  //console.log("ressss")
   // console.log(response)
    if (response.data.code==-1){
      router.push({name:'setting'});
    }else if (response.data.code==5){
     this.alert_ok('lang.PermissionDenied role :'+role)

    }else if (response.data.code==10){
      
      if(needbuy()){
        this.alert_ok('lang.You_need_to_purchase_to_continue_use')
        router.push({name:'help'});
      }
    
     }else {
      if (response.data.code!=undefined){
        var code=response.data.code
        if (code==2){
        router.push({name:'setting'});
         // this.alert_ok('token error please login')
        }
      }
    }
    return response;
  },
  error => {
    if (error && error.response) {
      switch (error.response.status) {
        case 400:
          break
        case 401:
          break
        case 403:
          return
        case 404:
          break
        case 408:
          break
        case 500:
          break
        case 504:
          break
        default:
          error.message = '未知错误'
      }
      message = Message.error(error.message)
    } else {
      message = Message.error(error.message)
    }
    return {error: -1, data: ''}
  }
)

export function AddKV(data) {
  return HTTP({
    url: '/api/kv',
    method: 'POST',
    data:data
  })
}

