import axios from 'axios';
import{getReqUrl,getLang,getValue,setValue} from "@/utils/pub"  
import VueCookies from 'vue-cookies'
axios.defaults.timeout = 5000;
axios.defaults.baseURL ="http://"+getReqUrl()+'/';
let AUTH_TOKEN=(function(){
  return getValue("token");
})();
let USERID=(function(){
  return getValue("userid");
})();

let staffid=(function(){
  return getValue("staffid");
})();
let role=(function(){
  return getValue("role");
})();
//http request 拦截器
axios.interceptors.request.use(
  config => {
    // const token = getCookie('名称');注意使用的时候需要引入cookie方法，推荐js-cookie
   // config.data = JSON.stringify(config.data);
    config.headers = {
      'Content-Type':'application/x-www-form-urlencoded'
    }
     
      config.params = {'userid':getValue("userid"),'staffid':getValue("staffid"),'role':getValue("role"),'apiversion':2.1}
    
    let token = getValue("token")
  //let csrf = store.getters.csrf
  if (token) {
   config.headers = {
    'Authorization': token,
    'Content-Type': 'application/x-www-form-urlencoded'
   }
  }

    return config;
  },
  error => {
    return Promise.reject(err);
  }
);


//http response 拦截器
axios.interceptors.response.use(
  response => {
    if(response.data.errCode ==2){
      router.push({
        path:"/login",
        querry:{redirect:router.currentRoute.fullPath}//从哪个页面跳转
      })
    }
    return response;
  },
  error => {
    return Promise.reject(error)
  }
)


/**
 * 封装get方法
 * @param url
 * @param data
 * @returns {Promise}
 */

export function fetch(url,params={}){
  return new Promise((resolve,reject) => {
    axios.get(url,{
      params:params
    })
    .then(response => {
      resolve(response.data);
    })
    .catch(err => {
      reject(err)
    })
  })
}


/**
 * 封装post请求
 * @param url
 * @param data
 * @returns {Promise}
 */

 export function post(url,data = {}){
  axios.defaults.headers.common['Authorization'] = getValue('token');
   return new Promise((resolve,reject) => {
     axios.post(url,data)
          .then(response => {
            resolve(response.data);
          },err => {
            reject(err)
          })
   })
 }

 /**
 * 封装patch请求
 * @param url
 * @param data
 * @returns {Promise}
 */

export function patch(url,data = {}){
  return new Promise((resolve,reject) => {
    axios.patch(url,data)
         .then(response => {
           resolve(response.data);
         },err => {
           reject(err)
         })
  })
}

 /**
 * 封装put请求
 * @param url
 * @param data
 * @returns {Promise}
 */

export function put(url,data = {}){
  return new Promise((resolve,reject) => {
    axios.put(url,data)
         .then(response => {
           resolve(response.data);
         },err => {
           reject(err)
         })
  })
}

